@import "../../styles/variable.scss";

.font-button {
  width: 32px;
  height: 32px;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  & > a {
    color: $white;
    text-decoration: none;
  }

  &-wrapper {
    border-radius: 50%;
    overflow: hidden;
  }
}
